import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Acao_preventiva_pa } from '../../../modelo/Acao_preventiva_pa'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'acao_preventiva_pa-dados-edit',   templateUrl: 'acao_preventiva_pa_dadosEdit.component.html' }) export class Acao_preventiva_paDadosEditComponent implements OnInit {    acao_preventiva_pa: Acao_preventiva_pa;    private sub: Subscription;    private errorMessage: string;     constructor(private router: Router,               private route: ActivatedRoute,               private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.acao_preventiva_pa.Acao_preventiva_pa";                 let id = +params['id'];         this.acao_preventiva_pa = new Acao_preventiva_pa();         this.acao_preventiva_pa.cd_acao_preventiva_pa = id;         o.objetoJson = this.acao_preventiva_pa;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a acao_preventiva_pa. ';       }      this.acao_preventiva_pa = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   }     onSubmit() {         }     onCancel(event: Event) {     event.preventDefault();     this.router.navigate(['/']);   }  }


